import { BpmnModel } from "./BpmnModel";
import { BpmnModelHelper } from "./BpmnModelHelper";
import { CatchEvent } from "./CatchEvent";

export class BoundaryEvent extends CatchEvent {
    clone(): BoundaryEvent {
        const clone = super.clone() as BoundaryEvent;
        clone.AttachedToRef = this.AttachedToRef;
        clone.CancelActivity = this.CancelActivity;

        return clone;
    }

    constructor(model: BpmnModel) {
        super(model);

        this.CancelActivity = true;
    }

    readonly ClrTypeID: string = "bpmn.BoundaryEvent";

    NewInstance(): BoundaryEvent {
        return new BoundaryEvent(this.Model);
    }

    AttachedToRef: string;
    CancelActivity: boolean;

    protected ResetBeforeLoadFrom(): void {
        super.ResetBeforeLoadFrom();

        this.AttachedToRef = null;
        this.CancelActivity = true;
    }

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "AttachedToRef", this.AttachedToRef);
        BpmnModelHelper.SetAttributeValue(obj, "CancelActivity", this.CancelActivity);

        return obj;
    }

    LoadFromJsonObject(obj: any): void {
        super.LoadFromJsonObject(obj);

        this.AttachedToRef = obj.AttachedToRef;
        this.CancelActivity = obj.CancelActivity;
    }
}